.container-service {
    width: 320px;
    font-family: "TT Lakes", sans-serif;
    font-size: 14px;
    padding:0;
    margin: 0 auto;
}

.body-service {
    margin:0 auto;
    padding: 0;
}

.h1-service {
    margin: 0;
    padding: 16px;
    font-size: 16px;
    font-weight: 400;
    text-transform: uppercase;
    line-height: 24px;
    letter-spacing: -0.2px;
    color: #7E7E82;
}

.header-service {
    width: auto;
    height: 56px;
    box-sizing: border-box;
    border-top: 1px solid #d9fff5;
    background: linear-gradient(to right, #f8f8f8, #ffffff);
}

.main-service {
    background-color: #F8F8F8;
}

.swiper-container-service {
    overflow: hidden;
    position: relative;
    padding-bottom: 35px;
}

.swiper-container-service::after {
    content: '';
    width: 24px;
    height: 200px;
    position: absolute;
    top:15px;
    right:-1px;
    background-image: url(./static/fade.png);
    z-index: 1;
}

.swiper-wrapper-service {
    padding-left: 16px;
    padding-top: 16px;
    width:auto;
}

.swiper-slide-service {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    box-sizing: border-box;
    background-color: white;
    max-width: 260px;
    min-height: 200px;
    flex-shrink: 0;
    border-radius: 6px;
    margin-right: 16px;

}

.swiper-pagination {
    position: absolute;
    bottom: 0;

}

.swiper-slide__service {
    padding-left: 16px;
    padding-top: 35px;
    padding-right: 16px;
    height: 35px;
}

.swiper-slide__service::before {
    position: absolute;
    width: 130px;
    top: 16px;
    left: 16px;
    content: " ";
    color: #7E7E82;
}

.swiper-slide__price {
    padding-left: 16px;
    padding-top: 30px;
    flex-basis: 100%;
}

.swiper-slide__price::before {
    position: absolute;
    top: 90px;
    left: 16px;
    content: "";
    color: #7E7E82;
}

.swiper-slide__time {
    padding-left: 16px;
    padding-top: 30px;
    flex-grow:1;
}

.swiper-slide__time::before {
    position: absolute;
    top: 150px;
    left: 16px;
    content: "";
    color: #7E7E82;
}

.swiper-slide__button {
    position: relative;
    margin-top: 15px;
    margin-right: 16px;
    border-radius: 16px;
    background-color: #FF3E79;
    width: 119px;
    height: 32px;
    text-transform: uppercase;
    font-weight: bold;
    color: white;
    border: none;
}

.order-button {
    position: relative;
    left: -10px;
    top: 1px;
}

.swiper-slide__button::after {
    content: '';
    position: absolute;
    top: 12px;
    right: 12px;
    width: 10px;
    height: 10px;
    background: url(./static/order_arrow.png) no-repeat;
}

.service-mobile_hidden {
    display: none;
}

.info {
  width: 288px;
  padding: 0 16px;
  display: flex;
  flex-direction: column;
  position: relative;
}

.info__text {
  font-size: 14px;
}

.info__get-commercial {
  margin-top: 16px;
  font-weight: 500;
  font-size: 16px;
  padding-bottom: 47px;
}

.info__get-commercial::after {
  content: '';
  width: 9px;
  height: 12px;
  background: url(./static/dropdown.png) no-repeat;
  position: absolute;
  bottom: 51px;
  right: 180px;
}

.footer{
  display: flex;
  border-top: 1px solid #D9FFF5;
  flex-direction: column;
  text-align: center;
  padding: 16px 0;
  font-size: 14px;
}

.footer__confidential {
  padding: 16px 0;
  opacity: 0.6;
}

.footer__rights{
  opacity: 0.6;
}

.footer__oferta {
  opacity: 0.6;
}

@media (min-width: 768px) {
    .container-service {
        min-width: 768px;
    }

    .header-service {
        height: 56px;
    }

    .h1-service {
        padding: 16px 24px;
    }

    .swiper-container-service {
        width: 768px;
        padding-bottom: 16px;
    }

    .swiper-wrapper-service{
        padding-left: 24px;
        padding-top: 16px;
        padding-right: 24px;
        max-width: 720px;
        display: flex;
        flex-direction: column;
    }

    .swiper-slide-service {
        min-width: 720px;
        min-height: 64px;
        display: flex;
        flex-wrap: nowrap;
        align-items: center;
        border-bottom: 1px solid #eaeaea;
        padding: 0 16px;
        margin: 0;
    }

    .swiper-slide__service {
        min-width: 55%;
        padding-top: 10px !important;
    }

    .swiper-slide__price {
        min-width: 10%;
    }

    .swiper-slide__time {
        min-width: 15%;
    }

    .swiper-slide__button {
        margin: 0;
        min-width: 120px;
        font-size: 12px;
    }

    .swiper-slide__time::before,
    .swiper-slide__price::before,
    .swiper-slide__service::before {
        display: none;
    }

    .swiper-slide__time,
    .swiper-slide__price,
    .swiper-slide__service {
        padding: 0;
        margin: 0;
        max-height: 64px;
    }

    .service-mobile_hidden {
        display: flex;
    }

    .service-caption {
        padding-left: 40px;
        padding-top: 30px;
        opacity: 0.7;
    }

    .caption-service {
        width: 52%;
    }

    .caption-price {
        width: 12%;
    }

    .caption-time {
        width: 15%;
    }

  .info {
    width: 421px;
    padding: 16px 24px 0 24px;
  }

  .info__get-commercial::after {
    bottom: 51px;
    right: 130px;
  }

  .footer{
    display: flex;
    flex-direction: row;
    padding: 32px 24px;
  }

  .footer__confidential {
    padding: 0 40px;
  }

  .footer__oferta {
    text-align: right;
    width: 213px;
  }

  .footer__rights {
    text-align: left;
  }

}

@media (min-width: 1440px) {

    .service-caption {
        padding-left: 56px;
    }

    .container-service {
        min-width: 1120px;
    }

    .h1-service {
        padding: 16px 32px;
    }

    .swiper-slide-service {
        min-width: 1056px;
        max-height: 72px;
        margin-right: 32px;
    }

    .swiper-wrapper-service{
        padding-left: 32px;
        min-width: 1056px;
    }

    .swiper-container-service {
        min-width: 1120px;
    }

    .swiper-slide__service {
        padding-left: 8px;
    }

    .caption-service {
        width: 53%;
    }

    .caption-price {
        width: 16.5%;
    }

    .caption-time {
        width: 15%;
    }

  .info {
    width: 421px;
    padding: 24px 32px 0 32px;
  }

  .info__get-commercial::after {
    bottom: 51px;
    right: 135px;
  }

  .footer{
    display: flex;
    flex-direction: row;
    padding: 32px 31px;
  }

  .footer__confidential {
    padding: 0 210px;
  }
}
