.body-index {
    min-height: 505px;
    /*padding: 0;
    margin: 0 auto;

     */
}

.main-blur {
  pointer-events: none;
  opacity: 0.5;
}

.body-scroll{
  overflow: hidden;
}

.left-menu {
  position: relative;
  top: 0;
  left:0;
  opacity: 1;
}

.main-wrapper{
  width: 320px;
  display:flex;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
}

.container {
    min-width: 320px;
    margin: 0 auto;
    font-family: "TT Lakes", serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-sizing: border-box;
}

.header {
    box-sizing: border-box;
    display: flex;
    min-width: 320px;
    min-height: 88px;
    width: 100%;
    justify-content: center;
    background: #FFFFFF;
    border-bottom: 1px solid #D9FFF5;
}

.head-buttons {
    display: flex;
    flex-basis: 100%;
}

button {
    border: none;
}

.head-buttons__left {
    display: flex;
    flex-basis: 40%;
    height: 100%;
    justify-content: flex-start;
    align-items: center;
}

.head-buttons__right {
    display: flex;
    flex-basis: 60%;
    justify-content: flex-end;
    align-items: center;
}

.head-buttons__burger {
    min-height: 40px;
    min-width: 40px;
    border-radius: 1.5em;
    background: #FF3E79 url(./static/burger.png) no-repeat center;
    margin-left: 16px;
    margin-right: 16px;
}

.head-buttons__logo {
    min-width: 100px;
    min-height: 50px;
}

.head-buttons__divider{
    min-height: 32px;
    min-width: 2px;
    background: url(./static/divider.png) no-repeat center;
    margin-right: 16px;

}

.head-buttons__phone {
    min-height: 40px;
    min-width: 40px;
    border-radius: 1.5em;
    background: #FF3E79 url(./static/phone.png) no-repeat center;
}

.head-buttons__chat {
    min-height: 40px;
    min-width: 40px;
    border-radius: 1.5em;
    background: #FF3E79 url(./static/chat.png) no-repeat center;
    margin: 0 1.2em;
}

.head-buttons__profile {
    min-height: 40px;
    min-width: 40px;
    border-radius: 1.5em;
    background: #FF3E79 url(./static/profile.png) no-repeat center;
    margin-right: 16px;
}

.head-buttons__repair {
    min-height: 40px;
    min-width: 40px;
    border-radius: 1.5em;
    background: #FF3E79 url(./static/repair.png) no-repeat center;
}

.head-buttons__checkstatus {
    min-height: 40px;
    min-width: 40px;
    display: flex;
    border-radius: 1.5em;
    background: #FF3E79 url(./static/checkstatus.png) no-repeat center;
    margin-right: 16px;
    margin-left: 16px;
}

.main-index {
    display:flex;
    flex-direction: column;
    min-width: 320px;
    width: 100%;
    height: 100%;
    background-color: #F8F8F8;
}

.top-wrapper {
    display: flex;
    flex-wrap: wrap;
    height: auto;
}

.desktop-buttons {
    justify-content: flex-end;
    flex-basis: 40%;
}

.desktop-buttons__layout{
    display: flex;
    align-items: center;
}

.desktop-buttons__link {
    color: black;
    text-decoration: none;
    font-size: 1.2em;
    font-weight: 500;
}

.desktop-buttons__link a {
    color: black;
}

.desktop-buttons__repair {
    min-height: 40px;
    min-width: 40px;
    border-radius: 1.5em;
    background: #FF3E79 url(./static/repair.png) no-repeat center;
    margin-right: 2em;
    margin-left: 0.7em;
}

.desktop-buttons__check-status {
    min-height: 40px;
    min-width: 40px;
    display: flex;
    border-radius: 1.5em;
    background: #FF3E79 url(./static/checkstatus.png) no-repeat center;
    margin-right: 2em;
    margin-left: 0.7em;
}

.title-index {
    display: flex;
    align-items: center;

}

.h1-index {
    font-size: 2em;
    font-weight: bold;
    line-height: 0;
}

a {
    text-decoration: none;
}

.title__title {
    margin: 1.5em 0 1.5em 1em;
    position: relative;
}

.title__title::before {
    position: absolute;
    top:0;
    left: -1em;
    content: '';
    height: 40px;
    width: 4px;
    background: url(./static/sideimage.png) no-repeat;

}

.title_image {
    margin-right: 10px;
    min-height: 30px;
    background: url(./static/sideimage.png) no-repeat center;

}

.navigation {
    box-sizing: border-box;
    height: auto;
    width: 100%;
    position: relative;
    z-index: 1;
}

.navigation__box {
    display: flex;
    justify-content: flex-start;
    overflow: scroll;
    height: auto;
    z-index: -1;
    margin-left: 1em;
}

.navigation__box::after {
    content: '';
    position: absolute;
    z-index: 5;
    width: 24px;
    height: 100%;
    top: 0;
    right: 0;
    background: url(./static/fade.png);
}

.navigation__box::-webkit-scrollbar {
    display: none;
}

.navigation__link {
    display: flex;
    position: relative;
    justify-content: center;
    align-items: center;
    min-height: 40px;
    flex-shrink: 0;
    font-size: 1.2em;
    font-weight: 500;
    color: #7E7E82;
    border: 1px solid transparent;
    border-radius: 6px;
    padding: 0 0.7em;

}
/*
.navigation__link:focus{
    background: #FFFFFF;
    border: 1px solid #B8FFEC;
    border-radius: 6px;
    z-index: 1;
}


 */
.active {
    background: #FFFFFF;
    border: 1px solid #B8FFEC;
    border-radius: 6px;
    z-index: 1;
}

.navigation__link:hover:not(.active) {
    color:#ff3e79;
    background-color:#fff;
    border:1px solid #ffe0e9
}

.navigation__fade-box {
    position: absolute;
    height: 40px;
    width: 100%;
}

.navigation__fade {
    position: absolute;
    z-index: 5;
    width: 24px;
    height: 100%;
    top: 0;
    right: 0;
    background: url(./static/fade.png);


}

.about {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    height: auto;

}

.about__wrapper {
    display: flex;
    flex-direction: column;
}

.about__article {
    width: 90%;
    font-size: 1em;
    letter-spacing: 0.02em;
    line-height: 1.2em;
    margin-left: 1em;
    margin-top: 2em;
}

.read-more {
    position:relative;
    display: flex;
    align-items: center;
    margin-bottom: 1em;
    margin-left: 1.7em;
    margin-top: 1.5em;
    width: 150px;
}

.read-more__button {
    font-family: inherit;
    font-size: 16px;
    font-weight: 500;
    letter-spacing: -0.1px;
    background-color: transparent;
    margin-left: 18px;
}

.read-more__button::before {
    position: absolute;
    top: 6px;
    left: 0;
    content: '';
    width: 7.41px;
    height: 10.3px;
    background: url(./static/read_more.png);
}

.hidden {
    display: none;
}

.mobile_hidden {
    display: none;
}

.menu-visible{
  display: block;
  z-index:2;
  position:absolute;
  top:0;
  left:0;
  box-shadow: 40px 0 40px -30px rgba(0, 0, 0, 0.3);
}

@media (min-width: 321px) {
  .main-wrapper {
    width: 320px;
  }
    .container {
        width: 320px;
        font-size: 14px;
    }
}

@media (min-width: 768px) {

  .main-wrapper {
    width: 768px;
  }

    .container {
        width: 768px;
    }

    .hidden {
        display: flex;
    }

    .title__title {
        padding-left: 5px;
    }

    .about {
        display: flex;
        width: 47%;
        height: 42%;
    }

    .about__wrapper {
        overflow: hidden;
        height: auto;
    }

    .about__article {
        line-height: 20px;
        margin-left: 1em;
        margin-top: 2em;
    }

    .main-index {
        height: 416px;
        position: relative;
    }

    .main-image {
        display: flex;
        width:360px;
        position: absolute;
        bottom:57px;
        right:25px;

    }

    .main-image__picture {
        width: 360px;
        height: 208px;
        border-radius: 6px;
    }

}

@media (min-width: 1440px) {

  .main-wrapper {
    width: 1440px;
  }
    .container {
        width: 1120px;
        height: 535px;
    }



    .header {
        display: none;
    }

    .mobile_hidden {
        display: flex;
    }

    .desktop_hidden {
        display: none;
    }

    .main-index {
        height: 535px;
    }

    .top-wrapper {
        padding-top: 20px;
    }

    .title-index {
        flex-basis: 60%;
    }

    .title__title {
        padding-left:18px;
    }

    .navigation__box  {
        flex-wrap: wrap;
        overflow: hidden;
    }

    .navigation__box , .about__article {
        margin-left: 2em;
    }

    .about {
        height:38%;
    }

    .read-more {
        margin-left: 3em;
    }

    .main-image {
        margin-right: 1em;
        width: auto;
        height: auto;
        bottom:30px;
        right:25px;
    }

    .main-image__picture {
        width: 532px;
        height: 307px;
        border-radius: 6px;
    }

}

