a {
    text-decoration: none;
    color: #1B1C21;
    cursor: pointer;
}

a:hover, button:hover {
    opacity: 0.6;
}

.sidemenu {
  font-family: "TT Lakes", serif;
  width: 320px;
  background: white;
  box-sizing: border-box;
  position: absolute;
  top: 0;
  left: -2000px;
  height: 100%;
  z-index: 3000;
}

.sidemenu-hidden {
  transform: translateX(2000px);
  transition-duration: 0.5s;
  background: white;
  z-index: 3000;
}

.sidemenu__wrapper {
  background: white;
  height: 100%;
  width: 85%;
  margin: 0 auto;
}

.sidemenu-header {
  display: flex;
  background: white;
}

.sidemenu-header__lt-left {
    width: 60%;
    padding-top: 20px;
}

.sidemenu-header__lt-right {
    width: 40%;
    padding-top: 20px;
}

.sidemenu-header__left-side {
    display: flex;
    align-items: center;
}

.sidemenu-header__mobile-burger {
    min-height: 40px;
    min-width: 40px;
    border-radius: 1.5em;
    background: #FF3E79 url(./static/burger_mod.png) no-repeat center;
    margin-right: 17px;
}

.sidemenu-header__mobile-logo {
    min-width: 100px;
    min-height: 50px;
    background: url(./static/logo.png) no-repeat center;
}

.sidemenu-header__right-side {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.sidemenu-header__mobile-search {
    min-height: 40px;
    min-width: 40px;
    border-radius: 1.5em;
    background: #FF3E79 url(./static/search.png) no-repeat center;
}

button {
    border: none;
}

.menu {
  height: 450px;
  background: white;
}

.menu__lt {
    height: 90%;
}

.menu__list {
    list-style: none;
    padding: 35px 0 0 0;
    margin:0;
}

.menu__list a {
    position: relative;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    display: block;
    padding: 16px 0;
    transition-duration: 0.6s;
}

.menu__list--active {
    opacity: 0.6;
}

.menu__list .menu__list--active::before {
    position:absolute;
    top:23px;
    left: -40px;
    content: "";
    width: 32px;
    background-color: #41F6D7;
    border: 2px solid #41F6D7;
    transform: rotate(90deg);
    border-radius: 7px 7px 0 0;
}

.sidemenu-footer{
  background: white;
}

.sidemenu-footer__buttons {
    display: flex;
    align-items: center;
    padding: 10px 0;
}

.sidemenu-footer__phone-button {
    min-height: 40px;
    min-width: 40px;
    border-radius: 1.5em;
    background: #FF3E79 url(./static/phone.png) no-repeat center;
    margin-right: 17px;
}

.sidemenu-footer__chat-button {
    min-height: 40px;
    min-width: 40px;
    border-radius: 1.5em;
    background: #FF3E79 url(./static/chat.png) no-repeat center;
    margin-right: 17px;
}

.sidemenu-footer__profile-button {
    min-height: 40px;
    min-width: 40px;
    border-radius: 1.5em;
    background: #FF3E79 url(./static/profile.png) no-repeat center;
}

.sidemenu-footer__email {
    padding: 10px 0;
}

.sidemenu-footer__phone-numb {
    font-size: 24px;
    font-weight: bold;
    padding: 10px 0;
}

.sidemenu-footer__languages {
    padding: 32px 0;
}

.sidemenu-footer__languages a {
    margin-right: 5px;
}

.sidemenu-footer__active--language {
    opacity: 0.6;
}

@media (min-width: 768px) {
  .menu{
    height: 480px;
  }
}

@media (min-width: 1440px) {
  .menu{
    height: 680px;
  }
  .sidemenu {
    position: relative;
    top:0;
    left:0;
  }
}


