.feedback{
    width: 320px;
  height: 100%;
    margin:0;
    padding:0;
    background: white;
    position: absolute;
    top: 0;
    right: -2000px;
    z-index:100;
}

.feedback--hidden{
  transform: translateX(-2000px);
  transition-duration: 0.3s;
}

button{
    cursor: pointer;
}

.feedback__header {
    height: 88px;
}

.feedback-buttons {
    padding: 24px 0 22px 12px;
}

.feedback-buttons__close-button {
    min-height: 40px;
    min-width: 40px;
    border-radius: 1.5em;
    background: #FF3E79 url(./static/close.png) no-repeat center;
    border: none;
}

.feedback__title-block{
    position: relative;
}

.feedback-title-block__title{
    font-size: 32px;
    font-weight: bold;
    padding-left: 16px;
}

.feedback-title-block__title::before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 4px;
    height: 40px;
    background: url(./static/sideimage.png);
}

.feedback__form-wrapper{
    padding: 24px 16px;
}

.feedback-form__input{
    height: 48px;
    width: 97%;
    margin-bottom: 16px;
    border-radius: 8px;
    border: 1px solid #EAEAEA
}

.feedback-form__input,
.feedback-form__textarea {
    padding-left: 16px;
}

.feedback-form__input::placeholder,
.feedback-form__textarea::placeholder{
    color: #B5B6BC;
    font-size: 16px;
}

.feedback-form__textarea{
    padding-top: 12px;
}

.feedback-form__textarea {
    height: 118px;
    width: 97%;
    margin-bottom: 16px;
    border-radius: 8px;
    border: 1px solid #EAEAEA;
}

.feedback-form__personal-data {
    color: #7E7E82;
    padding-bottom: 18px;
}

.feedback-link {
    text-decoration: none;
    color: #FF3E79;
}

.feedback-form__submit-button {
    position: relative;
    border-radius: 16px;
    background-color: #FF3E79;
    width: 119px;
    height: 32px;
    font-weight: bold;
    color: white;
    border: none;
    margin-left: 180px;
}

.feedback-form__submit-text {
    position: relative;
    left: -10px;
    top: 0;
}

.feedback-form__submit-button::after {
    content: '';
    position: absolute;
    top: 12px;
    right: 12px;
    width: 10px;
    height: 10px;
    background: url(./static/order_arrow.png) no-repeat;
}

@media (min-width: 1440px) {
    .feedback{
        width: 440px;
        padding-top: 25px;
    }

    .feedback-form__submit-button {
        margin-left: 300px;
    }

    .feedback__header{
        position: absolute;
        top:0;
        left: -80px;
    }
}
