.call{
    width: 320px;
    height: 100%;
    margin:0;
    padding:0;
    background: white;
    position: absolute;
    top: 0;
    right: -2000px;
    z-index:102;
}

/*.call-body {
  background: rgba(255, 255, 255, 0.5);

}

 */

.call--hidden{
  transform: translateX(-2000px);
  transition-duration: 0.3s;
}

button{
    cursor: pointer;
}

.call__header {
    height: 88px;
}

.call-buttons {
    padding: 24px 0 22px 12px;
}

.call-buttons__close-button {
    min-height: 40px;
    min-width: 40px;
    border-radius: 1.5em;
    background: #FF3E79 url(./static/close.png) no-repeat center;
    border: none;
}

.call__title-block{
    position: relative;
}

.call-title-block__title{
    font-size: 32px;
    font-weight: bold;
    padding-left: 16px;
}

.call-title-block__title::before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 4px;
    height: 40px;
    background: url(./static/sideimage.png);
}

.call__form-wrapper{
    padding: 24px 16px;
}

.call-form__input{
    height: 48px;
    width: 97%;
    margin-bottom: 16px;
    border-radius: 8px;
    border: 1px solid #EAEAEA
}

.call-form__input {
    padding-left: 16px;
}

.call-form__input::placeholder {
    color: #B5B6BC;
    font-size: 16px;
}

.call-form__personal-data {
    color: #7E7E82;
    padding-bottom: 18px;
}

.call-link {
    text-decoration: none;
    color: #FF3E79;
}

.call-form__submit-button {
    position: relative;
    border-radius: 16px;
    background-color: #FF3E79;
    width: 119px;
    height: 32px;
    font-weight: bold;
    color: white;
    border: none;
    margin-left: 180px;
}

.call-form__submit-text {
    position: relative;
    left: -10px;
    top: 0;
}

.call-form__submit-button::after {
    content: '';
    position: absolute;
    top: 12px;
    right: 12px;
    width: 10px;
    height: 10px;
    background: url(./static/order_arrow.png) no-repeat;
}

