@media (min-width: 1440px) {
    .call{
        width: 440px;
        padding-top: 25px;
    }

    .call-form__submit-button {
        margin-left: 300px;
    }

    .call__header{
        position: absolute;
        top:0;
        left: -80px;
    }
}
