.container-technics {
    width: 320px;
    font-family: "TT Lakes", sans-serif;
    padding:0;
    margin: 0 auto;
}

.body-technics {
    margin:0 auto;
    padding: 0;
}

.h1-technics {
    margin: 0;
    padding: 16px;
    font-size: 16px;
    font-weight: 400;
    text-transform: uppercase;
    line-height: 24px;
    letter-spacing: -0.2px;
    color: #7E7E82;
}

.header-technics {
    width: auto;
    height: 80px;
    box-sizing: border-box;
    border-top: 1px solid #d9fff5;
    background: linear-gradient(to right, #f8f8f8, #ffffff);
}

.main-technics {
    background-color: #F8F8F8;
}

.swiper-container-technics {
    overflow: hidden;
    position: relative;
    padding-bottom: 35px;
}

.swiper-container-technics::after {
    content: '';
    width: 24px;
    height: 160px;
    position: absolute;
    top:15px;
    right:-1px;
    background-image: url(./static/fade.png);
    z-index: 1;
}

.swiper-wrapper-technics {
    padding-left: 16px;
    padding-top: 16px;
    width:auto;
}

.swiper-slide-technics {
    box-sizing: border-box;
    background-color: white;
    position: relative;
    max-width: 240px;
    min-height: 160px;
    flex-shrink: 0;
    border: 1px solid #EAEAEA;
    border-radius: 6px;
    margin-right: 16px;

}

.swiper-slide:hover{
  opacity:0.6;
  cursor: pointer;
}

.swiper-pagination {
    position: absolute;
    bottom: 0;

}

.swiper-slide__image {
    height: 72px;
    display: flex;
    align-items: center;
    padding-left: 16px;
}

.swiper-slide__image::before {
    content: '';
    position: absolute;
    bottom: 16px;
    right: 16px;
    width: 40px;
    height: 40px;
    border: 2px solid #FF3E79;
    border-radius: 25px;
    background: url(./static/dropdown.png) no-repeat center;
}

.show-all-technics {
    position:relative;
    display: flex;
    align-items: center;
    padding-left: 24px;
    width: 150px;
    padding-bottom: 1.5em;
    padding-top: 1em;
}

.show-all__button-technics {
    font-family: inherit;
    font-size: 16px;
    font-weight: 500;
    letter-spacing: -0.1px;
    background-color: transparent;
    margin-left: 25px;
    border: none;
}

.show-all__button-technics::before {
    position: absolute;
    top: 1.4em;
    left: 32px;
    content: '';
    width: 7.41px;
    height: 10.3px;
    background: url(./static/read_more.png);
}

.show-all__button--arrows-up-technics::before {
    transform: rotate(180deg);
}

.show-all--mobile-hidden {
    display: none;
}

@media (min-width: 768px) {
    .container-technics {
        min-width: 768px;
    }

    .header-technics {
        height: 56px;
    }

    .h1-technics {
        padding: 16px 24px;
    }

    .technic-window-resizer {
        height: 350px !important;
    }

    .swiper-slide-technics {
        min-width: 224px;
        max-height: 72px;
        margin-right: 24px;
        margin-bottom: 16px;
    }

    .swiper-container-technics {
        height: 180px;
        padding-bottom: 16px;
    }

    .swiper-wrapper-technics{
        display: flex;
        flex-wrap: wrap;
        padding-left: 24px;
        padding-top: 24px;
    }

    .show-all--mobile-hidden {
        display: block;
    }
}

@media (min-width: 1440px) {
    .container-technics {
        min-width: 1120px;
    }

    .h1-technics {
        padding: 16px 32px;
    }

    .swiper-slide-technics {
        min-width: 240px;
        max-height: 72px;
        margin-right: 32px;
    }

    .swiper-wrapper-technics{
        padding-left: 32px;
    }

    .technic-window-resizer {
        height: 350px !important;
    }
}
